import { Table, Button } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { mock } from "@/api/mock";
import { account_get, get_by_user_nam } from "../../../api/acount";
import { account_log_get, role_log_get } from "../../../api/log";
import { get_id_user, repacle_id_user_name } from "../../../utils";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      role_log_get_handle(newVal);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 5,
    total = 20,
    showTotal = true,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 20,
    total: total || 0,
  });
  const columns = [
    { title: "ID", dataIndex: "id", key: "id" },
    { title: "操作模块", dataIndex: "module" },
    { title: "操作功能", dataIndex: "action" },
    { title: "操作人", dataIndex: "action_user_name" },
    {
      title: "操作时间",
      dataIndex: "action_time",
      // render: (action_time) =>
      //   dayjs(action_time).format("YYYY-MM-DD HH:mm:ss"),
    },
    {
      title: "操作备注",
      dataIndex: "action_remarks",
    },
  ];
  //获取日志列表
  const role_log_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await role_log_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      if (data.data.length > 0) {
        const user_ids = get_id_user(data.data, { key: "action_user" });
        const ids_names_data = await get_by_user_nam({
          user_ids: user_ids.toString(),
        });
        let ids_names = ids_names_data.data;
        data.data = repacle_id_user_name(data.data, {
          key: "action_user",
          data: ids_names,
          trans_key: "action_user_name",
        });
      }
      setData(data.data);
      setPagination({
        ...pagination,
        total: data.page.count,
        pageSize: data.page.page_size,
      });
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    setPagination({ ...pagination });
  };
  useEffect(async () => {
    role_log_get_handle();
  }, [pagination.current]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
